<template>
  <div class="game-card-container">
    <div class="word-card-container">
      <audio-card
        class="card"
        v-for="word in shuffledWordList"
        :key="'audio' + word.wordRank"
        :word="word"
        :isFlipped="word.isFlipped[0]"
        :isDisabled="word.isFlipped[0] || inputChecked"
        @flip-card="handleFlip(word, 0)"
      ></audio-card>
      <paraphrase-card
        class="card"
        v-for="word in wordList"
        :key="'paraphrase' + word.wordRank"
        :word="word"
        :isFlipped="word.isFlipped[1]"
        :isDisabled="word.isFlipped[1] || inputChecked"
        @flip-card="handleFlip(word, 1)"
      ></paraphrase-card>
    </div>
    <input-card
      class="input-card"
      :inputChecked="inputChecked"
      @spelling-success="handleSpellingSuccess"
    ></input-card>
  </div>
</template>

<script>
import inputCard from "./input-card.vue";
import paraphraseCard from "./paraphrase-card.vue";
import audioCard from "./audio-card.vue";

export default {
  components: {
    audioCard,
    paraphraseCard,
    inputCard,
  },
  data() {
    return {
      flippedCard: null,
      inputChecked: false,
    };
  },
  methods: {
    handleFlip(word, index) {
      this.$store.game.currentWord = word;
      console.log("ÂΩìÂâçÂçïËØç" + JSON.stringify(this.$store.game.currentWord));
      for (let item of this.wordList) {
        if (item.wordRank === word.wordRank) {
          console.log(item);
          this.$set(item.isFlipped, index, !item.isFlipped[index]);
        }
      }

      // Â¶ÇÊûú‰πãÂâçÊ≤°ÊúâÁøªËøáÂç°ÁâáÔºåÂ∞±ËÆ∞ÂΩï‰∏ãËøôÂº†Âç°Áâá
      if (!this.flippedCard) {
        this.flippedCard = { word: word, type: index };
      } else {
        // Â¶ÇÊûú‰πãÂâçÁøªËøá‰∏ÄÂº†Âç°ÁâáÔºåÂ∞±ÂíåËøôÂº†Âç°ÁâáÊØîËæÉ
        if (this.flippedCard.word.wordRank === word.wordRank) {
          // Â¶ÇÊûúÂåπÈÖçÔºå‰Ω†ÂèØ‰ª•Âú®ËøôÈáåÊâßË°åÂåπÈÖçÊó∂ÁöÑÈÄªËæëÔºå‰æãÂ¶ÇÊ†áËÆ∞Ëøô‰∏§Âº†Âç°Áâá‰∏∫Â∑≤ÁøªËΩ¨
          this.$store.game.currentWord = this.flippedCard.word.headWord;
          this.spelling = false;
          this.inputChecked = true;
          this.flippedCard = null;

          console.log("Matched!");
        } else {
          console.log("Not Matched!");
          // Â¶ÇÊûú‰∏çÂåπÈÖçÔºå‰Ω†ÂèØ‰ª•Âú®ËøôÈáåÊâßË°å‰∏çÂåπÈÖçÊó∂ÁöÑÈÄªËæëÔºå‰æãÂ¶ÇÂ∞ÜËøô‰∏§Âº†Âç°ÁâáÁøªÂõûÂéª
          let wordToFlip = word;
          console.log(word);
          setTimeout(() => {
            for (let item of this.wordList) {
              // ÁøªËΩ¨Á¨¨‰∫åÂº†Âç°Áâá
              if (item.wordRank === wordToFlip.wordRank) {
                console.log(item);
                this.$set(item.isFlipped, index, false);
              }
              // ÁøªËΩ¨‰πãÂâçÁöÑÂç°Áâá
              if (item.wordRank === this.flippedCard.word.wordRank) {
                console.log(item);
                // ‰ΩøÁî®‰πãÂâçÁöÑÂç°ÁâáÁöÑÁ±ªÂûãÊù•ÂÜ≥ÂÆöÁøªËΩ¨ÁöÑÁ¥¢Âºï
                this.$set(item.isFlipped, this.flippedCard.type, false);
              }
            }
            // Ê∏ÖÁ©∫ËÆ∞ÂΩïÁöÑÂç°Áâá
            this.spelling = true;
            this.inputChecked = false;
            this.flippedCard = null;
          }, 600);
        }
        // Ê∏ÖÁ©∫ËÆ∞ÂΩïÁöÑÂç°Áâá
      }
    },
    handleSpellingSuccess() {
      this.inputChecked = false;
      this.spelling = true;
    },
  },
  computed: {
    wordList() {
      return this.$store.game.wordList;
    },
    //Áî®Êù•Êâì‰π±Â±ïÁ§∫ÁöÑÂç°Áâá
    shuffledWordList() {
      // ‰ΩøÁî®.slice()Êù•ÂàõÂª∫‰∏Ä‰∏™Êñ∞Êï∞ÁªÑÔºå‰ª•Èò≤Ê≠¢Áõ¥Êé•‰øÆÊîπÂéüÂßãÊï∞ÊçÆ
      return this.wordList.slice().sort(() => Math.random() - 0.5);
    },
  },
};
</script>

<style>
.game-card-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.word-card-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* ÊØèË°åÊòæÁ§∫ 4 Âàó */
  gap: 20px;
}

.input-card {
  margin-top: 20px;
}
</style>
